import React from "react";
import styleCheckInfoWindow from "./CheckInfoWindow.module.css";
import {CategoryType, SettingsType} from "../../../App";
import {Option} from "../../../common/WeightsSelect/WeightsSelect";
import {Link} from "react-router-dom";

type CheckInfoWindowType = {
    settings: SettingsType
    setCreateNewTournament: (value: boolean) => void
    tournament: string
    location: string
    startTournamentDate: string
    endTournamentDate: string
    arrCategory: CategoryType[]
    sortCategory: (value: CategoryType) => Option[]
}

export const CheckInfoWindow = (props: CheckInfoWindowType) => {

    return (
        <div className={styleCheckInfoWindow.checkInfo}>
            <div className={styleCheckInfoWindow.textNewTournament}>  !</div>
            <div className={styleCheckInfoWindow.aboutTournament}>
                <div>: <span>{props.tournament}</span></div>
                <div> : <span>{props.location}</span></div>
                <div> /: {props.startTournamentDate === props.endTournamentDate
                    ? <span>{props.startTournamentDate}</span>
                    : <span>{props.startTournamentDate} / {props.endTournamentDate}</span>}</div>
            </div>
            <div className={styleCheckInfoWindow.categoriesAndSettings}>
                <div className={styleCheckInfoWindow.categoryAll}><span>:</span>
                    {props.arrCategory
                        .map(v => <div key={v.id}> {v.gender} / {v.categoryAthlete} ({v.age}): {props.sortCategory(v)
                            .map((v, index) => (index ? '; ' : '') + v.value)}</div>)}
                </div>
                <div className={styleCheckInfoWindow.settingsAll}><span> :</span>
                    <div>- - : {props.settings.tableNumb}</div>
                    {props.settings.place5_6 && <div> -   5-6 </div>}
                    {!props.settings.wrestlingSeparately && <div>- </div>}
                    {props.settings.leftHand && <div>-     </div>}
                    {props.settings.rightHand && <div>-     </div>}
                    {props.settings.final && props.settings.semifinal ?
                        <div>-     </div>
                        : props.settings.final && !props.settings.semifinal ?
                            <div>-   </div> : ''}
                </div>
            </div>
            <div className={styleCheckInfoWindow.warring}>!     " " 
                
                   .
            </div>
            <div className={styleCheckInfoWindow.buttonAcceptCancel}>
                <Link to={'/registrationMembers'} className={styleCheckInfoWindow.creatableTournament}> </Link>
                <button className={styleCheckInfoWindow.cancel} onClick={() => props.setCreateNewTournament(false)}></button>
            </div>
        </div>
    )
}